import type { Metadata } from 'next';
import Hero from '../components/hero/Hero.tsx';
import Features from '../components/features/Features.tsx';
import Testimonials from '../components/testimonials/Testimonials.tsx';

/**
 * Métadonnées de la page d'accueil
 */
export const metadata: Metadata = {
  title: 'BoltVision - Accueil',
  description: 'Page d\'accueil de l\'application BoltVision',
};

/**
 * Fonction asynchrone pour récupérer les données côté serveur
 * Utilisation du pattern de Server Component de Next.js
 */
async function getPageData() {
  return {
    status: 'ok',
  };
}

/**
 * Page d'accueil - Implémentation Next.js App Router
 * Server Component par défaut
 */
export default async function HomePage() {
  // Récupération des données côté serveur
  const data = await getPageData();

  return (
    <>
      <Hero />
      <main className="flex-1 container mx-auto p-4">
        <div className="mt-4 p-4 bg-blue-50 border border-blue-200 rounded-md">
          <p>Status: {data.status}</p>
        </div>
        <Features />
        <Testimonials />
      </main>
    </>
  );
}
